.file-explorer {
  height: 100%;
  overflow-y: auto;
  font-family: 'JetBrains Mono', monospace;
  font-size: 13px;
  color: var(--tokyo-fg);
}

.file-explorer.loading {
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading-message {
  color: var(--tokyo-fg-dark);
  font-style: italic;
}

.file-tree {
  padding: 8px 0;
}

.file-tree-item {
  width: 100%;
}

.file-item {
  display: flex;
  align-items: center;
  padding: 4px 8px;
  cursor: pointer;
  border-radius: 4px;
  margin: 1px 4px;
  transition: background-color 0.2s ease;
  user-select: none;
  min-height: 24px;
}

.file-item:hover {
  background-color: var(--tokyo-bg-highlight);
}

.file-item.file:hover {
  background-color: rgba(122, 162, 247, 0.1);
}

.file-item.directory {
  font-weight: 500;
}

.expand-icon {
  margin-right: 4px;
  font-size: 10px;
  color: var(--tokyo-fg-dark);
  transition: transform 0.2s ease;
  width: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.expand-icon.expanded {
  transform: rotate(90deg);
}

.file-icon {
  margin-right: 8px;
  font-size: 14px;
  flex-shrink: 0;
}

.file-name {
  flex: 1;
  font-size: 13px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.file-item.directory .file-name {
  color: var(--tokyo-fg);
}

.file-item.file .file-name {
  color: var(--tokyo-fg-dark);
}

.file-children {
  border-left: 1px solid rgba(65, 72, 104, 0.3);
  margin-left: 16px;
}

/* File type specific colors */
.file-item.file .file-name {
  color: var(--tokyo-fg-dark);
}

.file-item.file[data-extension="tsx"] .file-name,
.file-item.file[data-extension="jsx"] .file-name {
  color: var(--tokyo-blue);
}

.file-item.file[data-extension="ts"] .file-name,
.file-item.file[data-extension="js"] .file-name {
  color: var(--tokyo-yellow);
}

.file-item.file[data-extension="css"] .file-name,
.file-item.file[data-extension="scss"] .file-name {
  color: var(--tokyo-purple);
}

.file-item.file[data-extension="json"] .file-name {
  color: var(--tokyo-orange);
}

.file-item.file[data-extension="md"] .file-name {
  color: var(--tokyo-green);
}

.file-item.file[data-extension="html"] .file-name {
  color: var(--tokyo-red);
}

/* Scrollbar styling */
.file-explorer::-webkit-scrollbar {
  width: 6px;
}

.file-explorer::-webkit-scrollbar-track {
  background: transparent;
}

.file-explorer::-webkit-scrollbar-thumb {
  background: var(--tokyo-fg-gutter);
  border-radius: 3px;
}

.file-explorer::-webkit-scrollbar-thumb:hover {
  background: var(--tokyo-fg-dark);
}