.terminal {
  height: 100%;
  min-height: 0;
  display: flex;
  flex-direction: column;
  background: var(--tokyo-terminal);
  color: var(--tokyo-fg);
  font-family: 'JetBrains Mono', 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  overflow: hidden;
}

.terminal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 16px;
  background: #24283b;
  border-bottom: 1px solid #414868;
  min-height: 40px;
}

.terminal-header-left {
  display: flex;
  align-items: center;
  gap: 12px;
}

.terminal-header-right {
  display: flex;
  gap: 8px;
}

.terminal-control-btn {
  padding: 6px 16px;
  background: #2a2e42;
  border: 2px solid #7aa2f7;
  color: #c0caf5;
  font-size: 13px;
  cursor: pointer;
  border-radius: 6px;
  transition: all 0.3s ease;
  font-weight: 500;
  box-shadow: 0 0 10px rgba(122, 162, 247, 0.5);
}

.terminal-control-btn:hover {
  background: #363a52;
  transform: translateY(-1px);
  border-color: #ff9e00;
  box-shadow: 0 0 15px rgba(255, 158, 0, 0.7);
}

.terminal-control-btn.active {
  background: #ff9e00;
  color: #1a1b26;
  box-shadow: 0 0 20px rgba(255, 158, 0, 0.8);
}

.terminal-control-btn.stopping {
  background: #f7768e;
  color: #1a1b26;
  box-shadow: 0 0 20px rgba(247, 118, 142, 0.8);
  animation: pulse 1s infinite;
}

.terminal-control-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 20px rgba(247, 118, 142, 0.8);
  }
  50% {
    box-shadow: 0 0 30px rgba(247, 118, 142, 1);
  }
  100% {
    box-shadow: 0 0 20px rgba(247, 118, 142, 0.8);
  }
}

.terminal-content {
  flex: 1;
  padding: 0;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 0;
}

.xterm-container {
  width: 100%;
  height: 100%;
  flex: 1;
  overflow: hidden;
  background-color: #1a1b26;
  position: relative;
}

/* xterm.js specific styling */
.xterm {
  font-feature-settings: "liga" 0;
  position: relative;
  user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  height: 100%;
  width: 100%;
}

.xterm.focus,
.xterm:focus {
  outline: none;
}

.xterm .xterm-helpers {
  position: absolute;
  top: 0;
  z-index: 5;
  pointer-events: none;
}

.xterm .xterm-helper-textarea {
  position: absolute;
  opacity: 0;
  left: -9999em;
  top: 0;
  width: 0;
  height: 0;
  z-index: -5;
  white-space: nowrap;
  overflow: hidden;
  resize: none;
}

.xterm .composition-view {
  background: #000;
  color: #FFF;
  display: none;
  position: absolute;
  white-space: nowrap;
  z-index: 1;
}

.xterm .composition-view.active {
  display: block;
}

.xterm .xterm-viewport {
  background-color: transparent;
  overflow-y: scroll;
  cursor: default;
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
}

.xterm .xterm-screen {
  position: relative;
}

.xterm .xterm-screen canvas {
  position: absolute;
  left: 0;
  top: 0;
}

.xterm .xterm-scroll-area {
  visibility: hidden;
}

.xterm-char-measure-element {
  display: inline-block;
  visibility: hidden;
  position: absolute;
  top: 0;
  left: -9999em;
  line-height: normal;
}

.xterm.enable-mouse-events {
  cursor: default;
}

.xterm.xterm-cursor-pointer {
  cursor: pointer;
}

.xterm.column-select.focus {
  cursor: crosshair;
}

.xterm .xterm-accessibility,
.xterm .xterm-message {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
  color: transparent;
  pointer-events: none;
}

.xterm .live-region {
  position: absolute;
  left: -9999px;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.xterm-dim {
  opacity: 0.5;
}

.xterm-underline-1 { text-decoration: underline; }
.xterm-underline-2 { text-decoration: double underline; }
.xterm-underline-3 { text-decoration: wavy underline; }
.xterm-underline-4 { text-decoration: dotted underline; }
.xterm-underline-5 { text-decoration: dashed underline; }

.xterm-overline {
  text-decoration: overline;
}

.xterm-overline.xterm-underline-1 { text-decoration: overline underline; }
.xterm-overline.xterm-underline-2 { text-decoration: overline double underline; }
.xterm-overline.xterm-underline-3 { text-decoration: overline wavy underline; }
.xterm-overline.xterm-underline-4 { text-decoration: overline dotted underline; }
.xterm-overline.xterm-underline-5 { text-decoration: overline dashed underline; }

.xterm-strikethrough {
  text-decoration: line-through;
}

.xterm-screen .xterm-decoration-container .xterm-decoration {
  z-index: 6;
  position: absolute;
}

.xterm-screen .xterm-decoration-container .xterm-decoration.xterm-decoration-top-layer {
  z-index: 7;
}

.xterm-decoration-overview-ruler {
  z-index: 8;
  position: absolute;
  top: 0;
  right: 0;
  pointer-events: none;
}

.xterm-decoration-top {
  z-index: 2;
  position: relative;
}

/* Connection status styles */
.terminal-connection-status {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
}

.connection-indicator {
  font-size: 12px;
}

.connection-text {
  color: #a9b1d6;
}

.reconnect-btn {
  padding: 4px 8px;
  background: #ff9e00;
  color: #1a1b26;
  border: none;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.reconnect-btn:hover {
  background: #ffb84d;
  transform: translateY(-1px);
}

/* Terminal status in content area */
.terminal-status {
  padding: 4px 12px;
  background: rgba(36, 40, 59, 0.95);
  border-bottom: 1px solid #414868;
  font-size: 11px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-shrink: 0;
  min-height: 24px;
  height: 24px;
}

.terminal-status-connected {
  color: #9ece6a;
}

.terminal-status-disconnected {
  color: #f7768e;
}

/* Voice interface in terminal header */
.terminal-header-left .voice-interface {
  background: transparent;
  border: none;
  margin: 0;
  padding: 0;
  min-width: auto;
}

.terminal-header-left .voice-interface .voice-button {
  width: 32px;
  height: 32px;
  font-size: 14px;
}

/* Removed voice status styling since we're only showing the button */

/* Scrollbar styling */
.terminal-content::-webkit-scrollbar {
  width: 8px;
}

.terminal-content::-webkit-scrollbar-track {
  background: #1a1b26;
}

.terminal-content::-webkit-scrollbar-thumb {
  background: #414868;
  border-radius: 4px;
}

.terminal-content::-webkit-scrollbar-thumb:hover {
  background: #565f89;
}