.code-editor {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: var(--tokyo-bg);
}

/* Monaco Editor overrides */
.monaco-editor {
  font-family: 'JetBrains Mono', Monaco, Menlo, 'Courier New', monospace !important;
}

.monaco-editor .margin-view-overlays .line-numbers {
  color: var(--tokyo-fg-gutter) !important;
}

.monaco-editor .current-line ~ .line-numbers {
  color: var(--tokyo-fg-dark) !important;
}

/* Custom scrollbar styling for Monaco */
.monaco-editor .scrollbar .slider {
  background: rgba(122, 162, 247, 0.1) !important;
}

.monaco-editor .scrollbar .slider:hover {
  background: rgba(122, 162, 247, 0.2) !important;
}

.monaco-editor .scrollbar .slider.active {
  background: rgba(122, 162, 247, 0.3) !important;
}

/* Minimap styling */
.monaco-editor .minimap {
  border-left: none;
}

.monaco-editor .minimap-slider {
  background: rgba(122, 162, 247, 0.1) !important;
}

.monaco-editor .minimap-slider:hover {
  background: rgba(122, 162, 247, 0.2) !important;
}

/* Bracket matching */
.monaco-editor .bracket-match {
  background-color: rgba(122, 162, 247, 0.1);
  border: 1px solid var(--tokyo-blue);
}

/* Selection styling */
.monaco-editor .selected-text {
  background-color: rgba(122, 162, 247, 0.2) !important;
}

/* Find widget styling */
.monaco-editor .find-widget {
  background-color: var(--tokyo-bg-dark) !important;
  border: 1px solid var(--tokyo-fg-gutter) !important;
}

/* Suggest widget styling */
.monaco-editor .suggest-widget {
  background-color: var(--tokyo-bg-dark) !important;
  border: 1px solid var(--tokyo-fg-gutter) !important;
}

.monaco-editor .suggest-widget .monaco-list-row.focused {
  background-color: var(--tokyo-bg-highlight) !important;
}

/* Hover widget styling */
.monaco-editor .monaco-hover {
  background-color: var(--tokyo-bg-dark) !important;
  border: 1px solid var(--tokyo-fg-gutter) !important;
}

/* Context menu styling */
.monaco-menu {
  background-color: var(--tokyo-bg-dark) !important;
  border: 1px solid var(--tokyo-fg-gutter) !important;
}

.monaco-menu .monaco-action-bar .action-item.focused .action-label {
  background-color: var(--tokyo-bg-highlight) !important;
}