.editor-terminal-split {
  width: 100%;
  height: 100%;
  background-color: var(--tokyo-bg);
}

.editor-panel,
.terminal-panel {
  overflow: hidden;
}

.editor-container,
.terminal-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.editor-container {
  background-color: var(--tokyo-bg);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--tokyo-fg-dark);
  font-size: 14px;
}

.terminal-container {
  background-color: var(--tokyo-terminal);
}

/* Horizontal resize handle */
.resize-handle-horizontal {
  height: 4px;
  cursor: row-resize;
  background-color: var(--tokyo-bg-dark);
}

.resize-handle-horizontal:hover {
  background-color: var(--tokyo-fg-gutter);
}

.resize-handle-horizontal .resize-handle-inner {
  width: 100%;
  height: 100%;
}