.three-panel-layout {
  width: 100%;
  height: 100%;
  background-color: var(--tokyo-bg);
}

.panel-left,
.panel-center,
.panel-right {
  height: 100%;
  overflow: hidden;
}

.panel-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: var(--tokyo-bg);
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  background-color: var(--tokyo-bg-dark);
  border-bottom: 1px solid var(--tokyo-fg-gutter);
  min-height: 35px;
}

.panel-header h3 {
  margin: 0;
  font-size: 13px;
  font-weight: 500;
  color: var(--tokyo-fg-dark);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.panel-toggle {
  background: none;
  border: none;
  color: var(--tokyo-fg-dark);
  font-size: 12px;
  cursor: pointer;
  padding: 2px 6px;
  opacity: 0.7;
  transition: opacity 0.2s;
  font-family: 'JetBrains Mono', monospace;
}

.panel-toggle:hover {
  opacity: 1;
  color: var(--tokyo-cyan);
}

.panel-body {
  flex: 1;
  overflow: auto;
  background-color: var(--tokyo-bg);
}

/* Resize handles with orange glow */
.resize-handle {
  position: relative;
  background-color: var(--tokyo-bg-dark);
  transition: background-color 0.2s;
}

.resize-handle-vertical {
  width: 4px;
  cursor: col-resize;
}

.resize-handle-inner {
  position: absolute;
  inset: 0;
  opacity: 0;
  background: linear-gradient(90deg, var(--tokyo-orange), var(--tokyo-yellow));
  transition: opacity 0.2s;
}

.resize-handle:hover {
  background-color: var(--tokyo-fg-gutter);
}

.resize-handle:hover .resize-handle-inner {
  opacity: 0.8;
}

/* Panel specific styles */
.panel-left .panel-body {
  padding: 0;
}

.panel-center {
  background-color: var(--tokyo-bg);
}

.panel-right .panel-body {
  padding: 12px;
}

/* Collapsed state */
.panel-collapsed {
  width: 40px !important;
}

.panel-collapsed .panel-body {
  display: none;
}

.panel-collapsed .panel-header h3 {
  display: none;
}