.app-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: var(--tokyo-bg);
  overflow: hidden;
}

/* Header Bar */
.header-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--tokyo-bg-dark);
  border-bottom: 1px solid var(--tokyo-fg-gutter);
  padding: 6px 16px;
  height: 56px;
  flex-shrink: 0;
}

.header-left,
.header-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

.header-center {
  flex: 1;
  text-align: center;
}

.header-title {
  color: var(--tokyo-fg-dark);
  font-size: 14px;
  font-weight: 500;
  opacity: 0.8;
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 8px;
  height: 50px;
}

.logo-image {
  height: 50px;
  width: auto;
  object-fit: contain;
}

.logo-text {
  font-size: 18px;
  font-weight: 700;
  background: linear-gradient(135deg, var(--tokyo-orange) 0%, var(--tokyo-yellow) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.5px;
}

/* Header Menu */
.header-menu {
  display: flex;
  align-items: center;
  gap: 4px;
  margin-left: 24px;
}

.menu-item {
  padding: 4px 12px;
  background: transparent;
  border: 1px solid var(--tokyo-blue);
  color: var(--tokyo-fg-dark);
  font-size: 13px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 4px;
  white-space: nowrap;
  box-shadow: 0 0 10px rgba(122, 162, 247, 0.5);
}

.menu-item:hover {
  background-color: var(--tokyo-bg-highlight);
  color: var(--tokyo-fg);
  border-color: var(--tokyo-orange);
  box-shadow: 0 0 15px rgba(255, 158, 100, 0.6);
}

/* Header buttons */
.header-button {
  padding: 4px 12px;
  background: transparent;
  border: 1px solid var(--tokyo-purple);
  color: var(--tokyo-fg-dark);
  font-size: 12px;
  font-family: 'JetBrains Mono', monospace;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 4px;
  white-space: nowrap;
  box-shadow: 0 0 8px rgba(187, 154, 247, 0.5);
}

.header-button:hover {
  background-color: var(--tokyo-bg-highlight);
  color: var(--tokyo-fg);
  border-color: var(--tokyo-orange);
  box-shadow: 0 0 12px rgba(255, 158, 100, 0.6);
}

/* Status Bar */
.status-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--tokyo-bg-dark);
  border-top: 1px solid var(--tokyo-fg-gutter);
  padding: 4px 12px;
  height: 32px;
  flex-shrink: 0;
  gap: 10px;
  font-size: 12px;
}

.status-left,
.status-right {
  display: flex;
  gap: 8px;
  align-items: center;
}

.status-center {
  flex: 1;
  text-align: center;
}

.status-text {
  color: var(--tokyo-fg-dark);
  font-size: 11px;
  opacity: 0.8;
}

.status-button {
  padding: 2px 8px;
  background: transparent;
  border: 1px solid var(--tokyo-blue);
  border-radius: 4px;
  color: var(--tokyo-fg-dark);
  font-size: 11px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'JetBrains Mono', monospace;
  box-shadow: 0 0 8px rgba(122, 162, 247, 0.5);
}

.status-button:hover {
  background: var(--tokyo-bg-highlight);
  color: var(--tokyo-fg);
  border-color: var(--tokyo-orange);
  box-shadow: 0 0 12px rgba(255, 158, 100, 0.6);
}

/* Explorer content */
.explorer-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #24283b;
  border-right: 1px solid var(--tokyo-fg-gutter);
}

.explorer-actions {
  padding: 8px;
  border-top: 1px solid var(--tokyo-fg-gutter);
}

/* Action buttons */
.action-button {
  width: 100%;
  padding: 6px 12px;
  background: var(--tokyo-bg-highlight);
  border: 1px solid var(--tokyo-blue);
  border-radius: 4px;
  color: var(--tokyo-fg);
  font-size: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'JetBrains Mono', monospace;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  box-shadow: 0 0 10px rgba(122, 162, 247, 0.5);
}

.action-button:hover {
  background: var(--tokyo-bg-dark);
  border-color: var(--tokyo-orange);
  color: var(--tokyo-orange);
  box-shadow: 0 0 15px rgba(255, 158, 100, 0.6);
}

/* Editor placeholder */
.editor-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: var(--tokyo-fg-dark);
}

.editor-placeholder h2 {
  font-size: 18px;
  margin-bottom: 8px;
  color: var(--tokyo-fg);
}

.editor-placeholder p {
  font-size: 14px;
  opacity: 0.6;
}

/* Right panel content */
.right-panel-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 12px;
  gap: 12px;
  overflow-y: auto;
}

.react-bits-button {
  width: 100%;
  padding: 8px 16px;
  background: var(--tokyo-bg-highlight);
  border: 1px solid var(--tokyo-blue);
  border-radius: 6px;
  color: var(--tokyo-fg);
  font-size: 13px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'JetBrains Mono', monospace;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  box-shadow: 0 0 10px rgba(122, 162, 247, 0.5);
}

.react-bits-button:hover {
  background: var(--tokyo-bg-dark);
  border-color: var(--tokyo-orange);
  color: var(--tokyo-orange);
  box-shadow: 0 0 15px rgba(255, 158, 100, 0.6);
}

.preview-section {
  flex: 1;
  overflow: auto;
}

/* Editor with tabs layout */
.editor-with-tabs {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: var(--tokyo-bg);
}

.editor-with-tabs .code-editor {
  flex: 1;
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .header-menu {
    gap: 2px;
  }
  
  .menu-item {
    padding: 4px 8px;
    font-size: 12px;
  }
}

@media (max-width: 768px) {
  .header-menu {
    display: none;
  }
  
  .header-title {
    display: none;
  }
}

